import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//
import { BaseProductViewComponent } from './base-product-view/base-product-view.component';//-
import { BaseProductConfigComponent } from './base-product-config/base-product-config.component';//-
import { BaseProductModifyComponent } from './base-product-modify/base-product-modify.component';//-
const routes: Routes = [
    {
        path: 'base-product-view', component: BaseProductViewComponent,
    },
    {
        path: 'base-product-config', component: BaseProductConfigComponent,
    },
    {
        path: 'base-product-modify', component: BaseProductModifyComponent,
    },
   
];

export const BaseProductRouting: ModuleWithProviders = RouterModule.forChild(routes);
