import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { BasicVBean } from './bean/base-product-view.bean';
import { ConfirmationService } from 'primeng/primeng';// 
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
	selector: 'app-base-product-view',
	templateUrl: './base-product-view.component.html',
	styleUrls: ['./base-product-view.component.scss'],
	providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
export class BaseProductViewComponent implements OnInit {
	baseProductType: any;// -
	code: any;
	tabMesg: any = tableMessage;// 
	// bean
	basicVBean: BasicVBean = new BasicVBean();
	// 
	updateValue: any = [];
	// 
	msgs: Message[] = [];
	// 
	all_title: string = '';

	select_1: SelectItem[] = [];// 
	select_line: any = [];// 
	select_group: any = [];// 
	line_value: any = [{
		productCategoryId: ''
	}];// 
	group_value: string;// 
	isdisabled: boolean = true;
	// 
	showModel: string;
	headerTitle: string
	display: boolean = false;
	// 
	cars_2: any;
	// 
	total: string;
	// 
	pageSize: number = 10;
	// 
	first: number = 0;
	statuse: any = [];// 

	constructor(
		public productManageHttpService: ProductManageHttpService,
		public confirmationService: ConfirmationService,
		public commfunc: Commfunc
	) {
		this.codeValues(); // 
		this.baseProductType = this.code['BaseProductType'];
		this.statuse = this.code['fromStatusList'];
	}

	ngOnInit() {
		let session = window.sessionStorage;
		if (session.getItem('para')) {
			session.removeItem('para');
		}
		this.select_1 = [
			{ label: '', value: " " },
		];
		this.select_line = [
			{ label: '', value: " " },
		];
		this.select_group = [
			{ label: '', value: " " },
		];
		// this.statuse = [
		// 	{ label: '', value: "" },
		// 	{ label: '', value: "00" },
		// 	{ label: '', value: "01" },
		// 	{ label: '', value: "02" },
		// 	{ label: '', value: "04" },
		// 	{ label: '', value: "06" },
		// ];
		this.first = 0;
		this.queryClick();
		this.line_query();
	}

	// 
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret; // code
	}

	// 
	resetClick() {
		this.line_value = '';
		this.group_value = '';
		this.basicVBean = new BasicVBean();
		this.group_query();
	}
	// 
	querySecle() {
		this.basicVBean.pageSize = 10;
		this.basicVBean.pageNum = 1;
		this.first = 0;
		this.queryClick();
	}
	// 
	queryClick() {
		this.basicVBean.productLineId = this.line_value.productCategoryId;
		this.basicVBean.productGroupId = this.group_value;
		this.productManageHttpService.getBaseProduct(this.basicVBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.cars_2 = data.resultList;
				this.total = data.total;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}
	basicDetails(item) {
		this.display = true;
		this.headerTitle = '';
		this.showModel = '1';
		this.updateValue = item.productId;
	}
	detailsCall(param) {
		this.display = param;
	}
	// 
	deleteClick(item) {
		this.confirmationService.confirm({
			message: '?',
			accept: () => {
				// 
				let param = { baseProductId: item.productId };
				this.productManageHttpService.deleteBaseProduct(param).subscribe(data => {
					if (data.returnCode.code == "success") {
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						this.first = 0;
						this.queryClick();
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: "" });
				})
			}
		});
	}
	// 
	paginate(event) {
		// 
		this.basicVBean.pageSize = event.rows;
		// 
		let currentPage = event.page + 1;
		this.basicVBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.basicVBean.pageSize;
		// 
		this.queryClick();
		this.basicVBean.pageNum = 1;
	}
	// 
	line_query() {
		this.basicVBean = new BasicVBean();
		this.productManageHttpService.productLinePull(this.basicVBean).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productLineList.forEach(item => {
					let obj = {};
					obj['label'] = item.categoryName;
					obj['value'] = item;
					this.select_line.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}
	// 
	lineVelue() {
		this.select_group = [
			{ label: '', value: "" },
		];
		this.group_value = '';
		this.group_query()
	}
	// 
	group_query() {
		let parm = { productLineId: this.line_value.productCategoryId }
		this.productManageHttpService.productGroupPull(parm).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.productGroupList.forEach(item => {
					let obj = {};
					obj['label'] = item.categoryName;
					obj['value'] = item.productCategoryId;
					this.select_group.push(obj)
				});
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: "" });
		})
	}
	// 
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
	// 
	serveClick(item) {
		if (this.isdisabled == true) {
			this.isdisabled = false;
			this.confirmationService.confirm({
				message: '?',
				accept: () => {
					let dataA = {
						productId: item.productId
					}
					this.productManageHttpService.startWorkFlowLaunchForProduct(dataA).subscribe(data => {
						if (data.returnCode.code == "success") {
							this.queryClick();
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
						}
					}, error => {
						this.isdisabled = true;
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: "" });
					})
				},
				reject: () => {
					this.isdisabled = true;
				}
			});
		}
	}
	// 
	serveClicks(item) {
		if (this.isdisabled == true) {
			this.isdisabled = false;
			this.confirmationService.confirm({
				message: '?',
				accept: () => {
					let dataA = {
						productId: item.productId,
						productType: 'BASIC_PRODUCT',
						statusId: '06' 
					}
					this.productManageHttpService.updateProductStatus(dataA).subscribe(data => {
						if (data.returnCode.code == "success") {
							this.queryClick();
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
						} else {
							this.isdisabled = true;
							this.msgs = [];
							this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
						}
					}, error => {
						this.isdisabled = true;
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '', detail: "" });
					})
				},
				reject: () => {
					this.isdisabled = true;
				}
			});
		}
	}
}


